body{
    min-height: 100vh;
    overflow: hidden;
    border: 10px solid #333;
    background: gainsboro;
    border-top: none;
    margin-top: -23px;
}
h1{
    width: 100%;
    text-align: center;
    color:gainsboro;
    background: #333;
    padding: 10px;
}
section{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}
section form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
form button{
    margin: 10px !important;
}
.contents{
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 60%;
}
.contents>div{
    min-width: 60%;
}